@use 'base/mixins' as m;
@use 'base/variables' as v;

.paginacion{
    margin-top: 5rem;
    margin-bottom: 3rem;
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    @include m.tablet{
        justify-content: flex-end;
    }

    &__enlace{
        color: v.$azul;
        font-size: 1.8rem;
        &--texto{
            padding: 1rem;
            border-radius: .5rem;
            border: 1px solid v.$azul;
            &:hover{
                background-color: v.$azul;
                color: v.$blanco;
            }
        }

        &--numero{
            display: none;
            font-weight: bold;
            @include m.tablet{
                display: inline;
            }
        }

        &--actual {
            color: v.$blanco;
            font-weight: v.$bold;
            background-color: v.$azul;
            border-radius: 50%;
            width: 2rem;
            text-align: center;
            display: none;
         
            @include m.tablet {
                 display: inline;   
            }
         
            &:hover {
                cursor: not-allowed;
            }
        }
        
    }
}