@use 'base/mixins' as m;
@use 'base/variables' as v;

.listado-instructores{

    @include m.reset-lista;
    @include m.grid(1,1rem);

    &__instructor{
        color: v.$azul;
        border: 1px solid v.$azul;
        padding: 1rem;
        border-radius: 1rem;
        text-align: center;

        &:hover{
            cursor: pointer;
            background-color: v.$azul;
            color: v.$blanco;
        }

        &--seleccionado{
            @extend .listado-instructores__instructor;
            background-color: v.$azul;
            color: v.$blanco;
        }
    }

    
}